<template>
  <div v-show="this.show" id="aplayer"/>
</template>

<script>
import 'aplayer/dist/APlayer.min.css';
import APlayer from 'aplayer';
import {loadingClose} from "@/util/loading";
import {musicListApi} from "@/api/api";

export default {
  name: "MusicPlayer",
  data() {
    return {
      info: {
        fixed: true, // 
        listFolded: true, // 
        autoplay: true, // 
        preload: "auto", // 
        loop: "all", // all one none
        order: "random", //  list, random
        volume: 1//
      },
      show: false,
      hidden: true
    };
  },
  mounted() {
    // 
    this.createPlayer();
  },
  methods: {
    //jQuary,
    clickButton() {
      $('.aplayer-miniswitcher').click(() => {
        let aplayer = $('.aplayer-body');
        if (this.hidden) {
          aplayer.removeClass('aplayer-hover');
        } else {
          aplayer.addClass('aplayer-hover');
        }
        this.hidden = !this.hidden;
      })
      this.show = true;
    },
    createPlayer() {
      if (sessionStorage.getItem("musci_list") != null) {
        this.newPlayer(sessionStorage.getItem("musci_list"));
      } else {
        musicListApi()
            .then((result) => {
              this.newPlayer(result.data);
              sessionStorage.setItem("music_list", JSON.stringify(result.data));
            })
      }
    },
    newPlayer(audio) {
      const aPlayer = new APlayer({
        container: document.getElementById("aplayer"),
        //js
        lrcType: 1,
        audio: audio, // 
        ...this.info, // 
      });
      $('.aplayer-body').addClass('aplayer-hover');
      this.clickButton();
      loadingClose();
    }
  }
}

</script>

<style lang="less">
#aplayer {
  width: 320px; // 
}

.aplayer-body, .aplayer-list {
  color: var(--theme-skin-main);
}

.aplayer-hover {
  left: -66px !important;

  &:hover {
    left: 0 !important;
  }
}

.aplayer .aplayer-info .aplayer-controller .aplayer-bar-wrap {
  .aplayer-bar {
    height: 6px;
    border-radius: 10px;

    .aplayer-played {
      height: inherit;
      border-radius: inherit;

      .aplayer-thumb {
        height: 12px;
        width: 12px;
        right: 3px;
        top: 1px;
        box-shadow: 0 0 5px 0 rgba(0, 0, 0, .18);
        transition: all .35s
      }
    }

    .aplayer-loaded {
      height: inherit;
      border-radius: inherit;
    }
  }
}
</style>